import React, { useState, useEffect } from "react";
import { ThemeProvider } from "@mui/material/styles";
import "./styles/App.css";
import styles from "./styles/styles.module.css";
import { lightTheme, darkTheme } from "./styles/theme";
import { Routes, Route, useNavigate } from "react-router-dom";
import Home from "./components/Home";
import AddPersonForm from "./components/People/AddPersonForm";
import PeopleTable from "./components/People/PeopleList";
import Navbar from "./components/Navbar";
import { SnackbarProvider } from "notistack";
import Footer from "./components/Footer";

import { Box, Container } from "@mui/material"; // Import MUI components for layout

function App() {
  const navigate = useNavigate(); // Initialize navigate
  const userPrefersDark = window.matchMedia(
    "(prefers-color-scheme: dark)"
  ).matches;
  const [darkMode, setDarkMode] = useState(userPrefersDark);

  // Handle dark mode styles
  useEffect(() => {
    const body = document.body;

    if (darkMode) {
      body.classList.add(styles.darkMode, styles.transition);
      body.classList.remove(styles.lightMode);
    } else {
      body.classList.add(styles.lightMode, styles.transition);
      body.classList.remove(styles.darkMode);
    }
  }, [darkMode]);

  // Check for any redirected path after GitHub Pages 404
  useEffect(() => {
    const redirectPath = sessionStorage.getItem("redirectPath");
    if (redirectPath) {
      navigate(redirectPath); // Navigate to the stored path
      sessionStorage.removeItem("redirectPath"); // Clear the stored path after use
    }
  }, [navigate]);

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        minHeight: "100vh", // Ensures full page height
      }}
      className={darkMode ? styles.darkMode : styles.lightMode}
    >
      <SnackbarProvider maxSnack={3}>
        <ThemeProvider theme={darkMode ? darkTheme : lightTheme}>
          <Navbar darkMode={darkMode} darkModeFunction={setDarkMode} />
          <Container component="main" sx={{ flexGrow: 1, py: 3 }}>
            <Routes>
              <Route path="/" element={<Home />} />
              <Route path="/add-person" element={<AddPersonForm />} />
              <Route path="/people" element={<PeopleTable />} />
            </Routes>
          </Container>
        </ThemeProvider>
      </SnackbarProvider>
      <Footer className={darkMode ? styles.darkMode : styles.lightMode} />
    </Box>
  );
}

export default App;
