import axios from "axios";
import { Person } from "../Interfaces";

// Get the API address from the environment variable (default to empty string if not set)
const apiAddress: string = process.env.REACT_APP_API_ADDRESS || "";

// Function to fetch all people using axios
export async function getAllPeople(): Promise<Person[]> {
  try {
    const response = await axios.get<Person[]>(`${apiAddress}/People/all`);
    return response.data; // Return the array of people
  } catch (error) {
    console.error("Failed to fetch people:", error);
    throw error;
  }
}

export async function addPerson(newPerson: Person): Promise<number> {
  try {
    const response = await axios.post<{ id: number }>(
      `${apiAddress}/People/add`,
      newPerson
    );
    return response.data.id; // Return the new person's ID
  } catch (error) {
    console.error("Failed to add new person:", error);
    throw error;
  }
}

export async function deletePerson(idToDelete: number): Promise<boolean> {
  try {
    const response = await axios.delete<boolean>(
      `${apiAddress}/People/delete/${idToDelete}`
    );
    return response.data; // Return true if the request was successful
  } catch (error) {
    console.error("Failed to delete person:", error);
    throw error;
  }
}
