import * as React from "react";
import { Person } from "../../Interfaces";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Button,
  CircularProgress,
} from "@mui/material";
import styles from "../../styles/styles.module.css";
import { Cancel, PersonRemove } from "@mui/icons-material";

interface DeletePersonDialogProps {
  open: boolean;
  onClose: () => void;
  onDelete: () => void;
  person: Person | null;
  busyDeleting: boolean;
}

const DeletePersonDialog: React.FC<DeletePersonDialogProps> = ({
  open,
  onClose,
  onDelete,
  person,
  busyDeleting,
}) => {
  return (
    <Dialog open={open} onClose={onClose}>
      <DialogTitle>
        {person ? (
          <>
            Delete <b>{person.name}</b>?
          </>
        ) : (
          "Delete?"
        )}
      </DialogTitle>
      <DialogContent>
        <DialogContentText>
          This will permanently delete the user and cannot be undone.
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button
          startIcon={<PersonRemove />}
          className={styles.greenButton}
          onClick={onDelete}
          disabled={busyDeleting}
        >
          Delete
        </Button>
        {busyDeleting && <CircularProgress />}
        <Button
          startIcon={<Cancel />}
          className={styles.redButton}
          onClick={onClose}
          autoFocus
        >
          Cancel
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default DeletePersonDialog;
