import * as React from "react";
import { useState } from "react";
import { Person } from "../../Interfaces";
import { addPerson } from "../Requests";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Box,
  Button,
  CircularProgress,
} from "@mui/material";
import { useSnackbar } from "notistack";
import styles from "../../styles/styles.module.css";
import { Cancel, PersonAdd } from "@mui/icons-material";

interface AddPersonDialogProps {
  open: boolean;
  onClose: () => void;
  onPersonAdded: (person: Person) => void;
}

const AddPersonDialog: React.FC<AddPersonDialogProps> = ({
  open,
  onClose,
  onPersonAdded,
}) => {
  const [newPerson, setNewPerson] = useState<Person>({
    id: 0,
    name: "",
    age: 0,
    role: "",
    dateAdded: Math.floor(Date.now() / 1000),
  });
  const [addingPerson, setAddingPerson] = useState(false);
  const { enqueueSnackbar } = useSnackbar();

  const handleAddPerson = async () => {
    try {
      setAddingPerson(true);
      const newId = await addPerson(newPerson); // Now newId will be an int

      if (newId !== -1) {
        // Check if the returned ID is valid
        enqueueSnackbar(`Successfully added ${newPerson.name}`, {
          variant: "success",
        });
        onPersonAdded({ ...newPerson, id: newId }); // Use the new ID
        onClose(); // Close the dialog
        clearForm();
      } else {
        enqueueSnackbar("Failed to add person", { variant: "error" });
      }
    } catch (err) {
      enqueueSnackbar("An error occurred while adding the person.", {
        variant: "error",
      });
    } finally {
      setAddingPerson(false);
    }
  };

  const clearForm = () => {
    setNewPerson({
      id: 0,
      name: "",
      age: 0,
      role: "",
      dateAdded: Math.floor(Date.now() / 1000),
    });
  };

  return (
    <Dialog open={open} onClose={onClose}>
      <DialogTitle>Add New Person</DialogTitle>
      <DialogContent>
        <Box component="form" sx={{ mt: 2 }}>
          <TextField
            label="Name"
            fullWidth
            value={newPerson.name}
            onChange={(e) =>
              setNewPerson((prev) => ({ ...prev, name: e.target.value }))
            }
            required
            sx={{ mb: 2 }}
          />
          <TextField
            label="Age"
            fullWidth
            type="number"
            value={newPerson.age}
            onChange={(e) =>
              setNewPerson((prev) => ({ ...prev, age: Number(e.target.value) }))
            }
            required
            sx={{ mb: 2 }}
          />
          <TextField
            label="Role"
            fullWidth
            value={newPerson.role}
            onChange={(e) =>
              setNewPerson((prev) => ({ ...prev, role: e.target.value }))
            }
            required
            sx={{ mb: 2 }}
          />
        </Box>
      </DialogContent>
      <DialogActions>
        <Button
          startIcon={<PersonAdd />}
          onClick={handleAddPerson}
          disabled={addingPerson || !open}
          variant="contained"
          className={styles.greenButton}
        >
          Add Person
        </Button>
        {addingPerson && <CircularProgress />}
        <Button
          startIcon={<Cancel />}
          onClick={onClose}
          autoFocus
          className={styles.redButton}
        >
          Cancel
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default AddPersonDialog;
