import * as React from "react";
import { useState, useEffect } from "react";
import { Person } from "../../Interfaces";
import { getAllPeople, deletePerson } from "../Requests";
import { Delete, PersonAdd } from "@mui/icons-material";
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  IconButton,
  CircularProgress,
  Button,
} from "@mui/material";
import { useSnackbar } from "notistack";
import AddPersonDialog from "./AddPersonDialog";
import DeletePersonDialog from "./DeletePersonDialog";
import styles from "../../styles/styles.module.css";

function PeopleTable() {
  const [allPeople, setAllPeople] = useState<Person[]>([]);
  const [loadingPeople, setLoadingPeople] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const { enqueueSnackbar } = useSnackbar();

  // Delete User Dialog
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [selectedPersonToDelete, setSelectedPersonToDelete] =
    useState<Person | null>(null);
  const [busyDeleting, setBusyDeleting] = useState(false);

  // Add User Dialog
  const [addDialogOpen, setAddDialogOpen] = useState(false);

  const tableHeaders: string[] = [
    "Name",
    "Age",
    "Role",
    "Date Added",
    "Delete",
  ];

  // Fetch data on component mount
  useEffect(() => {
    const fetchData = async () => {
      try {
        const people = await getAllPeople();
        setAllPeople(people);
      } catch (err) {
        setError("Failed to load people");
      } finally {
        setLoadingPeople(false);
      }
    };

    fetchData();
  }, []);

  const handleDelete = async () => {
    if (selectedPersonToDelete) {
      try {
        setBusyDeleting(true);
        const success = await deletePerson(selectedPersonToDelete.id);
        if (success) {
          enqueueSnackbar(
            `Successfully removed ${selectedPersonToDelete.name}`,
            { variant: "success" }
          );
          setAllPeople(
            allPeople.filter(
              (person) => person.id !== selectedPersonToDelete.id
            )
          );
        } else {
          enqueueSnackbar(`Failed to remove ID ${selectedPersonToDelete.id}`, {
            variant: "error",
          });
        }
      } catch (err) {
        enqueueSnackbar("An error occurred while deleting the person.", {
          variant: "error",
        });
      } finally {
        setBusyDeleting(false);
        setDeleteDialogOpen(false);
        setSelectedPersonToDelete(null);
      }
    }
  };

  const handlePersonAdded = (newPerson: Person) => {
    setAllPeople([...allPeople, newPerson]);
  };

  if (error) {
    return <p>{error}</p>;
  }

  return (
    <Paper
      elevation={1}
      sx={{
        maxWidth: 800,
        mx: "auto",
        my: 4,
        p: [3, 2],
      }}
    >
      <h1>People List</h1>
      <Button
        variant="contained"
        startIcon={<PersonAdd />}
        onClick={() => setAddDialogOpen(true)}
        sx={{ mb: 2 }}
      >
        Add Person
      </Button>
      <Paper elevation={3} sx={{ mt: 1, p: 2 }}>
        {loadingPeople ? (
          <p>
            Loading... <CircularProgress />
          </p>
        ) : (
          <TableContainer sx={{ maxHeight: 460 }}>
            <Table stickyHeader>
              <TableHead>
                <TableRow>
                  {tableHeaders.map((header, index) => (
                    <TableCell key={index} className={styles.tableHead}>
                      {header}
                    </TableCell>
                  ))}
                </TableRow>
              </TableHead>
              <TableBody>
                {allPeople.map((person) => (
                  <TableRow key={person.id}>
                    <TableCell>{person.name}</TableCell>
                    <TableCell>{person.age}</TableCell>
                    <TableCell>{person.role}</TableCell>
                    <TableCell>
                      {new Date(person.dateAdded * 1000).toLocaleString()}
                    </TableCell>
                    <TableCell>
                      <IconButton
                        onClick={() => {
                          setSelectedPersonToDelete(person);
                          setDeleteDialogOpen(true);
                        }}
                      >
                        <Delete />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        )}
      </Paper>
      <AddPersonDialog
        open={addDialogOpen}
        onClose={() => setAddDialogOpen(false)}
        onPersonAdded={handlePersonAdded}
      />
      <DeletePersonDialog
        open={deleteDialogOpen}
        onClose={() => setDeleteDialogOpen(false)}
        onDelete={handleDelete}
        person={selectedPersonToDelete}
        busyDeleting={busyDeleting}
      />
    </Paper>
  );
}

export default PeopleTable;
