import {
  AppBar,
  Box,
  Toolbar,
  Typography,
  Container,
  Button,
  IconButton,
  Menu,
  MenuItem,
} from "@mui/material";
import {
  Adb,
  Menu as MenuIcon,
  DarkModeOutlined,
  LightModeOutlined,
} from "@mui/icons-material";
import { useState } from "react";
import { Link } from "react-router-dom"; // Import Link

interface NavbarProps {
  darkMode: boolean; // Add darkMode prop
  darkModeFunction: React.Dispatch<React.SetStateAction<boolean>>; // Function to toggle dark mode
}

const pages = ["Portfolio", "About", "Contact"];

const Navbar: React.FC<NavbarProps> = ({ darkMode, darkModeFunction }) => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const [mobileMenuAnchorEl, setMobileMenuAnchorEl] =
    useState<null | HTMLElement>(null);

  const handleDarkModeToggle = () => {
    darkModeFunction((prevMode) => !prevMode); // Toggle the dark mode state
  };

  // Unused because People was combined - For dropdowns
  /*const handleMenuClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };*/

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const handleMobileMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    setMobileMenuAnchorEl(event.currentTarget);
  };

  const handleMobileMenuClose = () => {
    setMobileMenuAnchorEl(null);
  };

  return (
    <AppBar position="static">
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <Link to="/" style={{ color: "inherit", textDecoration: "none" }}>
            <Adb sx={{ display: { xs: "none", md: "flex" }, mr: 1 }} />
          </Link>
          <Typography
            variant="h6"
            noWrap
            component={Link} // Use Link here
            to="/" // Link to the homepage
            sx={{
              mr: 2,
              display: { xs: "none", md: "flex" },
              fontFamily: "monospace",
              fontWeight: 700,
              letterSpacing: ".3rem",
              color: "inherit",
              textDecoration: "none",
            }}
          >
            Leoplureodon
          </Typography>

          {/* Mobile Menu Icon */}
          <Box sx={{ flexGrow: 1, display: { xs: "flex", md: "none" } }}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleMobileMenuOpen}
              color="inherit"
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={mobileMenuAnchorEl}
              anchorOrigin={{
                vertical: "top",
                horizontal: "left",
              }}
              keepMounted
              transformOrigin={{
                vertical: "top",
                horizontal: "left",
              }}
              open={Boolean(mobileMenuAnchorEl)}
              onClose={handleMobileMenuClose}
            >
              <MenuItem component={Link} to="/" onClick={handleMobileMenuClose}>
                Home
              </MenuItem>
              {pages.map((page) => (
                <MenuItem
                  key={page}
                  component={Link}
                  to={`/${page.toLowerCase()}`}
                  onClick={handleMobileMenuClose}
                >
                  {page}
                </MenuItem>
              ))}
              <MenuItem
                component={Link}
                to="/people"
                onClick={handleMobileMenuClose}
              >
                People
              </MenuItem>
              {/* Dark/Light Mode Toggle for Mobile */}
              <MenuItem
                onClick={() => {
                  handleMobileMenuClose();
                  handleDarkModeToggle();
                }}
              >
                {darkMode ? "Dark Mode" : "Light Mode"}{" "}
                {darkMode ? <DarkModeOutlined /> : <LightModeOutlined />}
              </MenuItem>
            </Menu>
          </Box>

          <Adb sx={{ display: { xs: "flex", md: "none" }, mr: 1 }} />
          <Box sx={{ flexGrow: 1, display: { xs: "none", md: "flex" } }}>
            <Button
              component={Link}
              to="/people"
              sx={{ my: 2, color: "white", display: "block" }}
            >
              People
            </Button>
            <Menu
              anchorEl={anchorEl}
              open={Boolean(anchorEl)}
              onClose={handleMenuClose}
            >
              <MenuItem onClick={handleMenuClose} component={Link} to="/people">
                List of People
              </MenuItem>
              <MenuItem
                onClick={handleMenuClose}
                component={Link}
                to="/add-person"
              >
                Add Person
              </MenuItem>
            </Menu>
            {pages.map((page) => (
              <Button
                key={page}
                component={Link}
                to={page.toLowerCase()}
                sx={{ my: 2, color: "white", display: "block" }}
              >
                {page}
              </Button>
            ))}
          </Box>
          {/* Dark Mode Toggle */}
          <Box
            sx={{ display: { xs: "none", md: "flex" }, alignItems: "center" }}
          >
            <Button
              sx={{ my: 2, color: "white", display: "block" }}
              onClick={handleDarkModeToggle}
            >
              {darkMode ? <DarkModeOutlined /> : <LightModeOutlined />}
            </Button>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
};

export default Navbar;
