import * as React from "react";
import { useState } from "react";
import { Button, TextField, Box, Paper } from "@mui/material";
import { Person } from "../../Interfaces";
import { addPerson } from "../Requests";
import { useSnackbar } from "notistack";

const AddPersonForm: React.FC = () => {
  const [name, setName] = useState<string>("");
  const [age, setAge] = useState<number | "">(""); // Start with empty string for form control
  const [role, setRole] = useState<string>("");
  const [error, setError] = useState<string | null>(null);
  const { enqueueSnackbar } = useSnackbar();

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    const newPerson: Person = {
      id: 0, // Assuming this will be generated by the backend
      name,
      age: Number(age),
      role,
      dateAdded: getCurrentEpoch(),
    };

    try {
      const success = await addPerson(newPerson);
      if (success) {
        enqueueSnackbar(`Sucessfully added ${newPerson.name}`, {
          variant: "success",
        });
        clearForm(); // Clear the form on success
      } else {
        setError(`Failed to add ${newPerson.name}`);
        enqueueSnackbar(`Failed to add ${newPerson.name}`, {
          variant: "success",
        });
      }
    } catch (err) {
      console.error(err);
      setError("An error occurred while adding the person.");
    }
  };

  function getCurrentEpoch(): number {
    const currentDate = new Date();
    const epochTime = Math.floor(currentDate.getTime() / 1000); // Divide by 1000 to get seconds
    return epochTime;
  }

  const clearForm = () => {
    setName("");
    setAge(""); // Reset to empty string for form
    setRole("");
    setError(null);
  };

  return (
    <Paper
      sx={{
        maxWidth: 800,
        mx: "auto", // Center the outer paper horizontally
        my: 4, // Adds vertical margin
        p: [3, 2], // Padding inside the outer paper
      }}
    >
      <h1>Add New Person</h1>

      <Paper
        sx={{
          mt: 2, // Margin top to separate inner paper from the title
          p: 2, // Padding inside the inner paper
        }}
      >
        <Box component="form" onSubmit={handleSubmit} sx={{ mt: 2 }}>
          {error && <p style={{ color: "red" }}>{error}</p>}
          <TextField
            label="Name"
            variant="outlined"
            fullWidth
            value={name}
            onChange={(e) => setName(e.target.value)}
            required
            sx={{ mb: 2 }}
          />
          <TextField
            label="Age"
            variant="outlined"
            type="number"
            fullWidth
            value={age}
            onChange={(e) => setAge(Number(e.target.value))}
            required
            sx={{ mb: 2 }}
          />
          <TextField
            label="Role"
            variant="outlined"
            fullWidth
            value={role}
            onChange={(e) => setRole(e.target.value)}
            required
            sx={{ mb: 2 }}
          />
          <Button type="submit" variant="contained" color="primary">
            Add Person
          </Button>
        </Box>
      </Paper>
    </Paper>
  );
};

export default AddPersonForm;
